					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">您充分知晓，以下情形中，我们收集、使用个人信息无需征得您的授权同意：</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（1）与国家安全、国防安全直接有关的；</span><br /><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（2）与公共安全、公共卫生、重大公共利益直接有关的；</span><br /><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（3）与犯罪侦查、起诉、审判和判决执行等直接有关的；</span><br /><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（4）出于维护个人信息主体或其他个人的生命、财产等重大合法权益但又很难得到您本人同意的；</span><br /><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（5）所收集的个人信息是个人信息主体自行向社会公众公开的；</span><br /><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（6）从合法公开披露的信息中收集的您的个人信息的；</span><br /><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（7）根据您的要求签订和履行合同所必需的；</span><br /><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（8）用于维护所提供的产品与/或服务的安全稳定运行所必需的，例如发现、处置服务的故障；</span><br /><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（9）学术研究机构基于公共利益开展统计或学术研究所必要，且对外提供学术研究或描述的结果时，对结果中所包含的个人信息进行去标识化处理的；</span><br /><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（10）法律法规规定的其他情形。</span></p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（二）员工福利购APP设备权限调用说明</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">员工福利购在提供服务过程中，需要申请调用您的一些设备敏感权限，您可以在设备的设置功能中选择关闭部分或全部敏感权限，从而拒绝员工福利购收集使用相关的个人信息。</span>
			</p>
