			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">我们申请获取的权限将与提供的服务功能保持一致，若我们提供的业务功能调整，我们申请的设备权限也将随之调整。我们在首次调用设备权限时会单独提示您是否开启并对权限申请目的进行解释，您可以在设备设置或员工福利购APP内对这些功能进行自主设置，员工福利购APP内设置具体路径为</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">“首页-我的-设置-系统权限管理”</span><span
					style="font-family:微软雅黑; font-size:10.5pt">。请您注意，您开启这些权限即代表您授权我们可以收集和使用这些个人信息来实现上述的功能，</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">您关闭权限即代表您取消了这些授权，则我们将不再继续收集您的这些个人信息，也无法为您提供上述与这些授权所对应的功能。您关闭权限的决定不会影响此前基于您的授权所进行的个人信息的处理。</span>
			</p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（三）您个人信息的使用规则</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">1、我们会根据本政策的约定并为实现我们的产品与/或服务功能对所收集的个人信息进行使用。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">2、我们通过技术手段对数据进行去标识化处理后，去标识化处理的信息将无法识别主体。</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">请您了解并同意，在此情况下我们有权使用已经去标识化的信息；在不透露您个人信息的前提下，我们有权对用户数据库进行分析并予以商业化的利用。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">3、我们会对我们的产品与/或服务使用情况进行统计，并可能会与公众或第三方共享这些统计信息，以展示我们的产品与/或服务的整体使用趋势。但这些统计信息不包含您的任何身份识别信息。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">4、当我们展示您的个人信息时，我们会采用包括内容替换、匿名处理方式对您的信息进行脱敏展示，以保护您的信息安全。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">5、当我们要将您的个人信息用于本政策未载明的其它用途时，或基于特定目的收集而来的信息用于其他目的时，会事先征求您的同意。</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">请您理解，我们向您提供的产品与/或服务将不断更新变化。如果您选择使用本政策中尚未列明的其他产品与/或服务时，在我们收集您的个人信息前，我们会通过业务协议、页面提示的方式向您详细说明信息收集的目的、方式、范围并征求您的明确同意。若您不同意提供前述信息，您可能无法使用该项服务，但不影响您使用其他服务。</span>
			</p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:15pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:15pt; font-weight:bold">二、我们如何使用Cookie和同类技术</span></p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（一）Cookie的使用</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">1、为实现您联机体验的个性化需求，使您获得更轻松的访问体验，我们会在您的计算机或移动设备上发送一个或多个名为Cookie的小数据文件，指定给您的Cookie是唯一的，它只能被将Cookie发布给您的域中的Web服务器读取。我们向您发送Cookie是为了简化您重复登录的步骤、存储您的浏览习惯和偏好等数据进而帮助您简化个人信息的填写、提供您的服务偏好设置、帮助您优化对广告的选择与互动、帮助判断您的登录状态以及账户或数据安全。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">2、我们不会将Cookie用于本政策所述目的之外的任何用途。您可根据自己的偏好管理或删除Cookie。您可以清除计算机上保存的所有Cookie，大部分网络浏览器会自动接受Cookie，但您通常可根据自己的需要来修改浏览器的设置以拒绝Cookie；另外，您也可以清除软件内保存的所有Cookie。但如果您这么做，您可能需要在每一次访问员工福利购网站时亲自更改用户设置，而且您之前所记录的相应信息也均会被删除，并且可能会对您所使用服务的安全性有一定影响。</span>
			</p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（二）网络Beacon和同类技术的使用</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">除Cookie外，我们还会在网站上使用网络Beacon等其他同类技术。我们的网页上常会包含一些电子图像（称为"单像素"GIF文件或"网络Beacon"）。我们使用网络Beacon的方式有：</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">1、通过在员工福利购网站上使用网络Beacon，</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">计算用户访问数量、停留时间，并通过访问Cookie辨认注册的员工福利购用户。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">2、通过得到的Cookie信息，为您提供个性化服务。</span></p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:15pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:15pt; font-weight:bold">三、我们如何共享、转让、公开披露您的个人信息</span></p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（一）委托处理</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">为了提高效率、降低成本，我们可能会委托关联公司或其他专业服务提供商代表我们来处理信息，例如我们会聘请服务提供商为我们提供基础设施技术服务、客户支持服务。对我们委托处理个人信息的公司、组织和个人，我们会通过书面协议、现场审计等方式要求其遵守严格的保密义务并采取有效的保密措施，要求其只能在委托范围内处理您的信息，而不得出于其自身利益使用您的信息。</span>
			</p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（二）共享</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">1、我们不会与员工福利购以外的任何公司、组织和个人共享您的个人信息，但以下情况除外：</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（1）事先获得您明确的同意或授权；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（2）根据适用的法律法规、法律程序的要求、强制性的行政或司法要求所必须的情况下进行提供；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（3）在法律法规允许的范围内，为维护员工福利购、员工福利购的关联公司或合作伙伴、您或其他员工福利购用户或社会公众利益、财产或安全免遭损害而有必要提供；</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（4）只有共享您的信息，才能实现我们的产品与/或服务的核心功能或提供您需要的服务；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（5）应您需求为您处理您与他人的纠纷或争议；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（6）符合与您签署的相关协议（包括在线签署的电子协议以及相应的平台规则）或其他的法律文件约定所提供；</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（7）基于学术研究而使用；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（8）基于符合法律法规的社会公共利益而使用。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">2、我们可能会向关联公司、合作伙伴等第三方共享您的个人信息，以保障为您提供的服务顺利完成。但我们仅会出于合法、正当、必要、特定、明确的目的共享您的个人信息，并且只会共享提供服务所必要的个人信息，该等第三方无权将共享的个人信息用于任何其他用途。</span><span
					style="font-family:微软雅黑; font-size:10.5pt">我们将按照法律法规及国家标准的要求以点击确认协议、具体场景下的点击确认动作等形式征求您的同意。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（1）我们会将您的个人</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">身份信息（真实姓名/名称、身份证信息/企业工商注册信息）</span><span
					style="font-family:微软雅黑; font-size:10.5pt">传输至电子认证服务机构(CA, Certificate Authority) ，用于 CA
					出具数字证书、调取本人数字证书进行电子签章。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（2）我们会将您的</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">真实姓名、身份证信息、手机号码、银行卡信息</span><span
					style="font-family:微软雅黑; font-size:10.5pt">，以及</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">人脸图像信息</span><span
					style="font-family:微软雅黑; font-size:10.5pt">共享给实名认证机构，用于校验实名身份。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（3）为了让您完成签约的目的，我们需要向您的签约相对方提供</span><span
					style="font-family:微软雅黑; font-size:10pt; font-weight:bold">合同编号与相关合同信息</span><span
					style="font-family:微软雅黑; font-size:10.5pt">，同时您的签约相对方可在签约过程中或签约成功后查看您的</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">实名认证姓名/名称</span><span
					style="font-family:微软雅黑; font-size:10pt; font-weight:bold">、</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">身份证号/企业统一信用代码、数字证书序列号、CA机构、签约时间和相关合同内容信息</span><span
					style="font-family:微软雅黑; font-size:10.5pt">。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（4）为了保证您及他人的合法权益，如您被他人投诉或您投诉他人，我们会将您的</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">姓名及有效证件号码、联系方式、投诉相关内容</span><span
					style="font-family:微软雅黑; font-size:10.5pt">提供给消费者权益保护部门及监管机关，以便及时解决投诉纠纷，但法律法规明确禁止提供的除外。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（5）在法律法规允许的范围内，在征得您明确同意的前提下，依据您的授权与第三方共享您的信息。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">3、对我们与之共享个人信息的公司、组织和个人，我们会与其签署严格的保密协定，要求他们按照我们的协议、本政策要求的保密和安全措施来处理个人信息。在个人敏感数据使用上，我们要求第三方采用数据脱敏和加密技术，从而更好地保护用户数据。</span>
			</p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（三）转让</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">我们不会将您的个人信息转让给任何公司、组织和个人，但以下情况除外：</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">1、事先获得您明确的同意或授权；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">2、根据适用的法律法规、法律程序的要求、强制性的行政或司法要求所必须的情况进行提供；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">3、符合与您签署的相关协议（包括在线签署的电子协议以及相应的平台规则）或其他的法律文件约定所提供；</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">4、在涉及合并、收购、资产转让或类似的交易时，如涉及到个人信息转让，我们会要求新的持有您个人信息的公司、组织继续受本政策的约束，否则,我们将要求该公司、组织重新向您征求授权同意。</span>
			</p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（四）披露</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">我们仅会在以下情况下，且采取符合业界标准的安全防护措施的前提下，才会公开披露您的个人信息：</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">1、根据您的需求，在您明确同意的披露方式下披露您所指定的个人信息；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">2、根据法律、法规的要求、强制性的行政执法或司法要求所必须提供您个人信息的情况下，我们可能会依据所要求的个人信息类型和披露方式公开披露您的个人信息。在符合法律法规的前提下，当我们收到上述披露信息的请求时，我们会要求必须出具与之相应的法律文件，如传票或调查函。我们坚信，对于要求我们提供的信息，应该在法律允许的范围内尽可能保持透明。我们对所有的请求都进行了慎重的审查，以确保其具备合法依据，且仅限于执法部门因特定调查目的且有合法权利获取的数据。</span>
			</p>
			<p style="font-size:15pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:15pt; font-weight:bold">四、我们如何保护和保存您的个人信息</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（一）我们保护您个人信息的技术与措施</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">我们非常重视个人信息安全，并采取一切合理可行的措施，保护您的个人信息：</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">1、数据安全技术措施</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">我们会采用符合业界标准的安全防护措施以及行业内通行的安全技术来防止您的个人信息遭到未经授权的访问、修改，避免您的个人信息泄露、损坏或丢失：</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（1）采取加密技术对您的个人信息进行加密存储，我们从安全管理、策略、过程、网络体系结构等诸多方面保障交易及个人信息安全。员工福利购平台部署于腾讯云环境中，腾讯云有完善的安全、灾备等机制，并且腾讯云环境已经通过信息安全登记保护三级认证。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（2）我们的网络服务采取了传输层安全协议等加密技术，通过https等方式提供浏览服务，确保您的个人信息在传输过程中的安全。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（3）在使用您的个人信息时，例如个人信息展示、个人信息关联计算，我们会采用包括内容替换、SHA256在内的多种数据脱敏技术增强个人信息在使用中的安全性。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（4）我们会采用恶意代码防范措施保护您的个人信息安全。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">2、为保护个人信息采取的其他安全措施</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">我们通过建立数据分类分级、数据安全管理规范、安全开发规范来管理规范个人信息的存储和使用：</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（1）我们采用严格的数据访问权限和多重身份认证技术控制和保护个人信息，通过与信息接触者签署严格的保密协议、监控和审计机制来对数据进行全面安全控制，避免数据被违规使用。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（2）我们采用代码安全检查、数据访问日志分析技术进行个人信息安全审计。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（3）我们建立了信息安全委员会并下设信息安全小组，由各小组信息安全专员负责个人信息安全事务。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（4）我们还会举办安全和隐私保护培训课程，加强员工对于保护个人信息重要性的认识。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">3、安全事件处置</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（1）为应对个人信息泄露、损毁和丢失等可能出现的风险，我们制定了多项制度，明确安全事件、安全漏洞的分类分级标准及相应的处理流程。我们为安全事件建立了专门的应急响应团队，按照安全事件处置规范要求，针对不同安全事件启动安全预案，进行止损、分析、定位、制定补救措施、联合相关部门进行溯源和打击。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（2）一旦发生个人信息安全事件，我们将按照法律法规的要求，及时向您告知安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。我们同时将及时将事件相关情况以邮件、信函、电话、推送通知等方式告知您，难以逐一告知个人信息主体时，我们会采取合理、有效的方式发布公告。同时，我们还将按照监管部门要求，主动上报个人信息安全事件的处置情况。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（3）如果您对我们的个人信息保护有任何疑问，可通过本政策最下方约定的联系方式联系我们。</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">如您发现自己的个人信息泄密，尤其是您的账户及密码发生泄露，请您立即通过本政策最下方</span><a
					href="https://esign.jd.com/pact/privacy.html#contactUsId"><span
						style="color:#0000ff; font-family:微软雅黑; font-size:10.5pt; font-weight:bold; text-decoration:underline">【如何联系我们】</span></a><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">约定的联系方式联络我们，以便我们采取相应措施。</span></p>
			<p style="font-size:13.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:13.5pt">&#xa0;</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">（二）您个人信息的保存</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">1、</span><span
					style="font-family:微软雅黑; font-size:10.5pt">我们将在中华人民共和国境内收集的个人信息存储在中华人民共和国</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">境内</span><span
					style="font-family:微软雅黑; font-size:10.5pt">。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">2、</span><span
					style="font-family:微软雅黑; font-size:10.5pt">我们承诺为了保证您的消费者权益，除非法律法规另有规定，您的</span><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">个人信息我们将保存至您账号注销之日后的一个月</span><span
					style="font-family:微软雅黑; font-size:10.5pt">。当您的个人信息超出我们所保存的期限后，我们会对您的个人信息进行删除或匿名化处理。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">3、</span><span
					style="font-family:微软雅黑; font-size:10.5pt">如果我们终止服务或运营，我们会至少提前三十天通知您，并在终止服务或运营后对您的个人信息进行删除或匿名化处理。</span>
			</p>
			<p style="font-size:15pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:15pt; font-weight:bold">五、您的权利</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">员工福利购非常重视您对个人信息的关注，并尽全力保护您对于您个人信息访问、更正、删除以及撤回同意的权利，以使您拥有充分的能力保障您的隐私和安全。您的权利包括：</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">1、访问您的个人信息</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">您有权随时访问您的个人信息，法律法规规定的例外情况除外。如果您想行使数据访问权，可以通过以下方式自行访问：</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（1）您的账号信息</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">移动端APP内操作路径：</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">账户名称、手机号码：首页－我的－访问头像、用户ID、手机号码；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">账户密码、安全信息：首页－我的-重置密码。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">电脑网页端操作路径：</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">您可以点击网页顶部“用户中心”、网页顶部最右侧“个人账户/企业账户”进行账户相关的操作。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（2）您的合同信息</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">移动端APP内操作路径：</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">已签署合同信息：首页－签约完成。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">待签署合同信息：首页-待签署。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">待他人签署合同信息：首页-待他人签署。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">电脑网页端操作路径：</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">已签署合同信息：首页－已完成。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">待签署合同信息：首页－待我签。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">待他人签署合同信息：首页-待他人签。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">已创建的合同信息：首页-我发起的。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">如果您无法通过上述链接访问这些个人信息，您可以随时通过本政策</span><a
					href="https://esign.jd.com/pact/privacy.html#contactUsId"><span
						style="color:#0000ff; font-family:微软雅黑; font-size:10.5pt; text-decoration:underline">【如何联系我们】</span></a><span
					style="font-family:微软雅黑; font-size:10.5pt">约定的联系方式联络我们。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">我们将在15个工作日内回复您的访问请求。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">对于您在使用我们的产品或服务过程中产生的其他个人信息，只要我们不需要过多投入，我们会向您提供。如果您想行使数据访问权，请通过本政策</span><a
					href="https://esign.jd.com/pact/privacy.html#contactUsId"><span
						style="color:#0000ff; font-family:微软雅黑; font-size:10.5pt; text-decoration:underline">【如何联系我们】</span></a><span
					style="font-family:微软雅黑; font-size:10.5pt">约定的联系方式联络我们。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">2、更正您的个人信息</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">当您发现我们处理的关于您的个人信息有错误时，您有权要求我们做出更正。您可以随时通过本政策</span><a
					href="https://esign.jd.com/pact/privacy.html#contactUsId"><span
						style="color:#0000ff; font-family:微软雅黑; font-size:10.5pt; text-decoration:underline">【如何联系我们】</span></a><span
					style="font-family:微软雅黑; font-size:10.5pt">约定的联系方式联络我们。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">我们将在15个工作日内回复您的更正请求。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">3、删除您的个人信息</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">在以下情形中，您可以向我们提出删除个人信息的请求：</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（1）如果我们处理个人信息的行为违反法律法规；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（2）如果我们收集、使用您的个人信息，却未征得您的同意；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（3）如果我们处理个人信息的行为违反了与您的约定；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（4）如果您不再使用我们的产品或服务，或您注销了账号；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（5）如果我们不再为您提供产品或服务。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">收到您的删除请求后，我们会根据您及相关法律法规的要求进行后续删除处理并向您反馈结果。若我们决定响应您的删除请求，我们还将同时通知从我们获得您的个人信息的实体，要求其及时删除，除非法律法规另有规定，或这些实体获得您的独立授权。当您从我们的服务中删除信息后，我们可能不会立即备份系统中删除相应的信息，但会在备份更新时删除这些信息。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">4、改变您授权同意的范围</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">您可以通过删除信息、关闭设备功能、在员工福利购软件中进行隐私设置等方式改变部分您授权我们继续收集个人信息的范围或撤回您的授权。您也可以通过注销具体产品、注销账号的方式，撤回我们继续收集您个人信息的全部授权。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">请您理解，每个业务功能需要一些基本的个人信息才能得以完成，当您撤回同意或授权后，我们无法继续为您提供撤回同意或授权所对应的服务，也不再处理您相应的个人信息。但您撤回同意或授权的决定，不会影响此前基于您的授权而开展的个人信息处理。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">5、注销</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">您可以在我们的产品与/或服务中直接申请注销账号或注销您申请的具体员工福利购服务功能，您可以通过移动端APP访问“首页-我的-设置-注销账号”完成账号注销，还可以发送邮件至esign@jd.com或拨打我们的客服电话010-86592510与我们联系，经核实您的注销信息、您所使用的产品/服务的履行情况等具体细节后，我们对您的注销账号申请进行处理。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt; font-weight:bold">6、响应您的上述请求</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">为保障安全，您可能需要提供书面请求，或以其他方式证明您的身份。我们可能会先要求您验证自己的身份，然后再处理您的请求。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">我们将在十五个工作日内作出答复。如您不满意，还可以通过以下途径投诉：发送邮件至esign@jd.com、拨打我们的客服电话010-86592510。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">对于您合理的请求，我们原则上不收取费用，但对多次重复、超出合理限度的请求，我们将视情况收取一定成本费用。对于那些无端重复、需要过多技术手段（例如，需要开发新系统或从根本上改变现行惯例）、给他人合法权益带来风险或者非常不切实际（例如，涉及备份磁带上存放的信息）的请求，我们可能会予以拒绝。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">在以下情形中，我们将无法响应您的请求：</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（1）与个人信息控制者履行法律法规规定的义务相关的；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（2）与国家安全、国防安全有关的；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（2）与公共安全、公共卫生、重大公共利益直接有关的；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（3）与刑事侦查、起诉、审判和执行判决等直接有关的；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（4）有充分证据表明个人信息主体存在主观恶意或滥用权利的；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（5）出于维护个人信息主体或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（6）响应您的请求将导致您或其他个人、组织的合法权益受到严重损害的；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（7）涉及商业秘密的。</span></p>
			<p style="font-size:15pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:15pt; font-weight:bold">六、我们如何处理儿童的信息</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">1、员工福利购非常重视对儿童个人信息的保护。我们的产品与/或服务仅面向成人。</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">2、对于经父母或法定监护人同意而收集儿童个人信息的情况，我们只会在受到法律允许、父母或监护人明确同意或者保护儿童所必要的情况下使用或公开披露此信息。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">3、如果我们发现自己在未事先获得可证实的父母或法定监护人同意的情况下收集了儿童的个人信息，或儿童的监护人撤回其同意的，则会设法尽快删除相关数据。</span>
			</p>
			<p style="font-size:15pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:15pt; font-weight:bold">七、本政策如何更新</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">1、为给您提供更好的服务以及随着员工福利购业务的发展，本政策也会随之更新。但未经您明确同意，我们不会削减您依据本政策所应享有的权利。我们会通过在员工福利购网站、员工福利购移动端上发出更新版本并在生效前通过网站公告或以其他适当方式提醒您相关内容的更新，也请您访问员工福利购以便及时了解最新的政策。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">2、对于重大变更，我们还会提供更为显著的通知（我们会通过包括但不限于邮件、短信或在浏览页面做特别提示等方式，说明政策的具体变更内容）。</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">本政策所指的重大变更包括但不限于：</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（1）我们的业务和服务模式发生重大变化。如处理个人信息的目的、处理的个人信息类型、个人信息的使用方式、业务功能等；</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（2）我们在所有权结构、组织架构等方面发生重大变化。如业务调整、破产并购等引起的所有者变更等；</span>
			</p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（3）个人信息共享、转让或公开披露的主要对象发生变化；</span></p>
			<p style="font-size:10.5pt; line-height:115%; margin:0pt 0pt 10pt; text-align:justify"><span
					style="font-family:微软雅黑; font-size:10.5pt">（4）您参与个人信息处理方面的权利及其行使方式发生重大变化；</span></p>
